.tab-flex {
  margin-left: 20px;
  max-height: 680px;
  @media (max-width: 426px) {
    margin-left: 10px;
  }
  .tab {
    overflow: hidden;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    @media (max-width: 426px) {
      display: flex;
      flex-flow: inherit;
    }
  }

  .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    background: transparent;
    outline: none;
    border: 2px solid #fff;
    border-radius: 20px;
    color: white;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
    margin: 10px;
    @media (max-width: 769px) {
      font-size: 8px;
    }
    @media (max-width: 426px) {
      padding: 10px 12px;

      font-size: 7px;
      margin: 5px;
    }
  }

  .tab button:hover {
    background-color: black;
  }

  .tab button.active {
    background-color: white;
    color: black;
  }

  .tabcontent {
    padding: 6px 12px;

    border-top: none;
    .title-info {
      color: white;
      font-size: 40px;
      @media (max-width: 1026px) {
        font-size: 25px;
      }
    }
    .title-par {
      width: 70%;
      font-size: 25px;
      line-height: 35px;
      @media (max-width: 1026px) {
        font-size: 16px;
        line-height: 20px;
      }
      @media (max-width: 426px) {
        width: 100%;
      }
    }
  }
  .info-tab {
    position: relative;
    left: 230px;
    top: -350px;
    @media (max-width: 769px) {
      left: 150px;
      top: -300px;
    }
    @media (max-width: 426px) {
      left: 0px;
      top: 0px;
    }
  }
}
