.user {
  @media (max-width: 426px) {
    display: flex;
    flex-flow: column nowrap;
  }
}

.skill-block {
  padding-top: 30px;
  padding-bottom: 40px;
  .skill-icons {
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    @media (max-width: 769px) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
    @media (max-width: 426px) {
      grid-template-columns: 1fr 1fr;
    }

    gap: 9px 13px;
    .icons-element {
      text-align: center;
      .icon-img {
        height: 100px;
        width: 100%;
      }
    }
  }
}
