#becgraun {
  width: 100%;
  min-height: 100vh;
  // background-image: url("img/1pu.png");
  background-size: cover;
  background-position: center;
}

.navbar {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 10;
  padding: 20px 0;
  @media (max-width: 1026px) {
    width: 100%;
  }
}

.navbar img {
  width: 50px;
  margin: 30px 0;
  cursor: pointer;
}

.user {
  display: flex;
  align-items: center;
}

// .user img {
//   width: 100px;
//   margin-left: 50px;
//   cursor: pointer;
// }

button {
  padding: 8px 25px;
  background: transparent;
  outline: none;
  border: 2px solid #fff;
  border-radius: 20px;
  color: white;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
  @media (max-width: 769px) {
    font-size: 8px;
  }
  @media (max-width: 426px) {
    padding: 5px 15px;
  }
}
.container {
  width: 50%;
  min-height: 700px;
  background: rgba(86, 86, 86, 0.3);
  backdrop-filter: blur(10px);
  @media (max-width: 1780px) {
    width: 60%;
  }
  @media (max-width: 1505px) {
    width: 70%;
  }
  @media (max-width: 1305px) {
    width: 80%;
  }
  @media (max-width: 1026px) {
    width: 77%;
  }
  @media (max-width: 769px) {
    width: 70%;
  }
  @media (max-width: 426px) {
    width: 64%;
  }
}

.margin-left {
  margin-left: 0px !important;
  @media (max-width: 1149px) {
    margin-left: 0px;
  }
}

.info {
  padding-top: 200px;
  padding-left: 30px;
  /* display: absolute; */
  width: 90%;
  color: #fff;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

h1 {
  color: #f0eef1;
  font-size: 50px;
  @media (max-width: 1026px) {
    font-size: 30px;
  }
}
h2 {
  color: #f0eef1;
  font-size: 30px;
  @media (max-width: 1026px) {
    font-size: 20px;
  }
}

p {
  color: #f0eef1;
  font-size: 11px;
  line-height: 18px;
  @media (max-width: 1026px) {
    font-size: 9px;
  }
}

