::placeholder {
  color: #ccc;
}
.info-my-job {
  @media (max-width: 1026px) {
    margin-top: -200px;
  }
  @media (max-width: 426px) {
    margin-top: 0px;
  }
  .my-job {
    text-align: center;
    font-size: 40px;
    @media (max-width: 426px) {
      font-size: 20px;
    }
  }
  .slider {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 10px 10px;
    margin: 10px;
    @media (max-width: 769px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 425px) {
      grid-template-columns: 1fr;
    }

    .cart-work {
      text-align: center;
      border: 3px solid #fff;

      border-radius: 20px;

      .cart-img {
        width: 100%;

        opacity: 1;
        pointer-events: auto;
        cursor: pointer;
        border-radius: 20px 20px 0 0;
      }
      .info-work {
        font-size: 20px;
      }
      .btn-link {
        margin-bottom: 10px;
      }
    }
  }
}

.slider .preview {
}

#prev,
#next {
  cursor: pointer;
}

.preview {
  /* display: flex;
    align-items: center; */
  margin: 0 30px;
}

.preview .active {
  display: block;
  border: 3px solid #fff;
  width: 100px;
  opacity: 1;
  pointer-events: auto;
  cursor: pointer;
}

a {
  cursor: default;
  color: white;
  text-decoration: none;
  font-size: 16px;
}
